var app = require("../../common/common.js");
var id;
export default {
	components: {},
	data: function() {
		return {
			pageLoad: false,
			pageHide: false,
			pageData: {},
			total: 0
		}

	},
	onLoad: function(option) {
		uni.setNavigationBarTitle({
			title: "购物车",
		})
		this.getPage();
	},
	onShow: function() {
		if (this.pageHide) {
			this.getPage();
		}
	},
	onHide: function() {
		this.pageHide = true;
	},
	methods: {
		getPage: function() {
			wx.cloud.init()
			const db = wx.cloud.database()
			db.collection('shopcar').get({
				success: res => {
					console.log(res);
					this.total = 0
					this.pageData = res.data
					this.pageData.forEach(item => {
						this.total += item.total * 1
					})
					this.pageLoad = true;
					// this.pageLoad = true;表示数据完成可以渲染
				},
				fail: err => {
					wx.showToast({
						icon: 'none',
						title: '查询记录失败'
					})
					console.error('[数据库] [查询记录] 失败：', err)
				}
			})
			this.pageLoad = true
		},
		plusCart: function(index, amount) {
			this.pageData[index].amount++
			this.pageData[index].total = this.pageData[index].amount * 1 * this.pageData[index].price
			this.total = 0
			this.pageData.forEach(item => {
				this.total += item.total
			})
		},
		minusCart: function(index, amount) {
			if (amount > 1) {
				this.pageData[index].amount--
				this.pageData[index].total = this.pageData[index].amount * 1 * this.pageData[index].price
				this.total = 0
				this.pageData.forEach(item => {
					this.total += item.total
				})
			}

		},
		deleteCart: function(id,index) {
			wx.cloud.init()
			const db = wx.cloud.database()
			db.collection('shopcar').doc(id).remove().then(res=>{
				this.pageData.splice(index, 1)
				console.log(this.pageData,11111111)
			})

		}
	},
}
